
.videobg {
    position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 90%;
  width: 90%;
  height: auto;
  transform: translateX(-50%) translateY(-50%);
  z-index: -1;
}

.header {
  font-style: italic;
  background: linear-gradient(90deg, rgb(13, 84, 151) 0%, rgba(11, 165, 212, 0.404) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.subTitle {
  font-weight: 700;
  margin-top:  10px;
}
@media (max-width: 992px) {
  .header{
  background: url("../images/background.JPG" ) !important;
  }
  video {
    visibility: hidden;
  }
}

